<template>
  <div>
    <el-button-group class="mb-2">
      <el-button 
        :disabled="!selectedCase || loading"
        size="mini" 
        icon="icon icon-folder-open" 
        plain 
        @click="goToDetail" />
      <el-button 
        :disabled="loading"
        size="mini" 
        icon="icon icon-spinner11" 
        plain 
        @click="retrieveCase(params)" />
      <el-button 
        :disabled="loading"
        size="mini" 
        icon="icon icon-plus" 
        plain 
        @click="TOGGLE_NEW_CASE_DIALOG" />
    </el-button-group>
    <el-table
      :data="cases.items"
      v-loading="loading"
      element-loading-text="Loading..."
      size="mini"
      border
      highlight-current-row
      @current-change="handleCurrentChange">
      <el-table-column type="index" width="50" align="center" fixed="left" />
      <el-table-column
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :align="c.align"
        :sortable="c.sortable"
        :width="c.width"
        :fixed="c.fixed">
        <template slot-scope="scope">
          <span v-if="['created_at'].includes(c.prop)" :title="scope.row[c.prop]">
            {{ scope.row[c.prop] | moment('from', 'now') || '-' }}
          </span>
          <span v-else>{{ scope.row[c.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :disabled="loading"
      :total="cases.length"
      :page-sizes="[20, 50, 100]"
      :page-size="params.page_size"
      :current-page="params.page_number"
      class="mt-2 right"
      layout="total, sizes, prev, pager, next, jumper"
      background
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>
    <new-case-dialog />
  </div>
</template>

<script>
import { mapMutations, mapActions, mapGetters } from 'vuex'
import NewCaseDialog from '@/components/iDecision/Dashboard/NewCaseDialog'

export default {
  name: 'idecision-dashboard',

  components: {
    NewCaseDialog
  },

  data: () => ({
    params: {
      page_size: 20,
      page_number: 1
    },
    columnDefs: [
      { label: 'Case ID', prop: 'case_id', sortable: true, width: 120 },
      { label: 'Tên khách hàng', prop: 'customer_name', sortable: true },
      { label: 'Sản phẩm', prop: 'product', sortable: true, width: 150 },
      { label: 'Thời gian tạo', prop: 'created_at', width: 140 },
      { label: 'CVTĐ', prop: 'appraiser', width: 100, sortable: true },
      { label: 'Status', prop: 'status', align: 'center', width: 100, sortable: true }
    ],
    selectedCase: null
  }),

  computed: {
    ...mapGetters('iDecision', ['cases', 'loading'])
  },

  created() {
    this.retrieveCase(this.params)
  },
    
  methods: {
    ...mapActions('iDecision', ['retrieveCase']),
    ...mapMutations('iDecision', ['TOGGLE_NEW_CASE_DIALOG']),

    handleCurrentChange(val) {
      this.selectedCase = val
    },

    currentPageChange(val) {
      this.params.page_number = val
      this.retrieveCase(this.params)
    },

    sizeChange(val) {
      this.params.page_size = val
      this.retrieveCase(this.params)
    },

    goToDetail() {
      const caseId = this.selectedCase.case_id
      this.$router.push({ path: `/idecision/case/${caseId}` })
    }
  }
}
</script>



