<template>
  <el-dialog 
    :visible.sync="dialogNewCaseVisible"
    :before-close="TOGGLE_NEW_CASE_DIALOG"
    :width="'25%'"
    title="New case" 
  >
    <el-form :model="form" :rules="rules" ref="newCaseForm" label-width="80px" size="mini" label-position="left" >
      <el-form-item label="Case ID" prop="case_id">
        <el-input v-model="form.case_id" />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="submit('newCaseForm', false)">Add</el-button>
      <el-button size="small" type="primary" @click="submit('newCaseForm', true)">Add & Go to detail</el-button>
      <el-button size="small" @click="TOGGLE_NEW_CASE_DIALOG">Cancel</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters('iDecision', ['dialogNewCaseVisible'])
  },

  data: () => ({
    form: {
      case_id: '1234',
    },
    rules: {
      case_id: [
        { required: true, message: 'Please input case Id', trigger: 'blur' },
        { min: 8, max: 8, message: 'Length should be 8', trigger: 'blur' }
      ],
    }
  }),

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_CASE_DIALOG']),
    ...mapActions('iDecision', ['retrieveCase', 'addCase']),

    submit(formName, goToDetail) {
      this.$refs[formName].validate((valid) => {
        console.log(goToDetail)
        if (valid) this.addCase({ newCase: this.form, goToDetail }) 
        else return false
      })
    }
  }
}
</script>



